          wt: "在Word的编辑状态，要在文档中添加符号，第一步应该打开40___?",
          radio:'B',
          da: [
            {
              radio: "A",
              daan: "A、文件选"
            },
            {
              radio: "B",
              daan: "B、开始选"
            },
            {
              radio: "C",
              daan: "C、文档选"
            },
            {
              radio: "D",
              daan: "D、代码选"
            }
          ]
        }
      ]
    }
    },
    methods:{
        openShow(index){
            this.show = true
            this.index = index
            this.question = this.list[this.index-1]
        },
        previous(){
          if(this.index == 1){
            this.index = 40
          }else{
            this.index--
          }
            this.question = this.list[this.index-1]
        },
        next(){
          if(this.index == 40){
            this.index = 1
          }else{
            this.index++
          }
          this.question = this.list[this.index-1]
        },
        leftClick(){
          this.$router.push('/')
        },
        rightClick(){
          this.$router.push('practice')
        }
    }
}
